import axios from "axios";
import {
    GET_ERRORS,
    TURNER_ADD,
    TURNER_UPDATE
} from "./types";

import config from "../config/config";

const serverUrl = config.serverUrl;

export const addTurner = (turnerData, history) => dispatch => {

    axios
        .post(serverUrl + "/api/turner-add", turnerData)
        .then(res =>
            dispatch({
                type: TURNER_ADD,
                payload: res,
            })
        ).catch(err =>
        dispatch({
            type: GET_ERRORS,
            payload: err.response.data
        })
    );
};

export const updateTurner = (turnerData) => dispatch => {

    axios
        .post(serverUrl + "/api/turner-update", turnerData)
        .then(res =>
            dispatch({
                type: TURNER_UPDATE,
                payload: res,
            })
        ).catch(err =>
        dispatch({
            type: GET_ERRORS,
            payload: err.response.data
        })
    );
};
