import {
    TURNER_ADD,
    TURNER_UPDATE
} from "../actions/types";
const isEmpty = require("is-empty");
const initialState = {
    isAuthenticated: false,
    turner: {},
    loading: false,
};
export default function(state = initialState, action) {
    switch (action.type) {
        case TURNER_ADD:
            return {
                isAuthenticated: !isEmpty(action.payload),
                turner: action.payload
            };
        case TURNER_UPDATE:
            return {
                isAuthenticated: !isEmpty(action.payload),
                turner: action.payload,
            };
        default:
            return state;
    }
}
