import React from 'react'
import classnames from "classnames";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { updateTurner } from "../../actions/turnerActions";
import { withRouter } from "react-router-dom";
import { toast } from 'react-toastify';
import $ from 'jquery';

import 'react-toastify/dist/ReactToastify.css';

class TurnerUpdateModal extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            id: this.props.record.id,
            name: this.props.record.name,
            from: this.props.record.from,
            to: this.props.record.to,
            use: this.props.record.use,
            errors: {},
        };
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.record) {
            this.setState({
                id: nextProps.record.id,
                name: nextProps.record.name,
                from: nextProps.record.from,
                to: nextProps.record.to,
                use: nextProps.record.use,
            })
        }
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            });
        }

        if (nextProps.turner !== undefined
            && nextProps.turner.turner !== undefined
            && nextProps.turner.turner.data !== undefined
            && nextProps.turner.turner.data.message !== undefined
            && nextProps.turner.turner.data.success) {
            $('#update-turner-modal').modal('hide');
            toast(nextProps.turner.turner.data.message, {
                position: toast.POSITION.TOP_CENTER
            });
        }
    }

    onChange = e => {
        if (e.target.id === 'turner-update-name') {
            this.setState({ name: e.target.value });
        }
        if (e.target.id === 'turner-update-from') {
            this.setState({ from: e.target.value });
        }
        if (e.target.id === 'turner-update-to') {
            this.setState({ to: e.target.value });
        }
        if (e.target.id === 'turner-update-use') {
            this.setState({ use: e.target.value });
        }
    };

    onTurnerUpdate = e => {
        e.preventDefault();

        const newTurner = {
            _id: this.state.id,
            name: this.state.name,
            from: this.state.from,
            to: this.state.to,
            use: this.state.use
        };

        this.props.updateTurner(newTurner);
    };

    render() {
        const { errors } = this.state;
        return (
            <div>
                <div className="modal fade" id="update-turner-modal">
                    <div className="modal-dialog modal-lg">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h4 className="modal-title">Update Turner</h4>
                                <button type="button" className="close" data-dismiss="modal">&times;</button>
                            </div>
                            <div className="modal-body">
                                <form noValidate onSubmit={this.onTurnerUpdate} id="update-turner">
                                    <input
                                        onChange={this.onChange}
                                        value={this.state.id}
                                        id="turner-update-id"
                                        type="text"
                                        className="d-none"/>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="name">Name</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.name}
                                                id="turner-update-name"
                                                type="text"
                                                error={errors.name}
                                                className={classnames("form-control", {
                                                    invalid: errors.name
                                                })}/>
                                            <span className="text-danger">{errors.name}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="use">Use</label>
                                        </div>
                                        <div className="col-md-9">
                                            <select id="turner-update-use" onChange={this.onChange} value={this.state.use} error={errors.use}
                                                className={classnames("form-control", {
                                                    invalid: errors.use
                                                })}>
                                                <option value={1}>Enable</option>
                                                <option value={0}>Disable</option>
                                            </select>
                                            <span className="text-danger">{errors.use}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="from">From</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.from}
                                                id="turner-update-from"
                                                type="text"
                                                error={errors.from}
                                                className={classnames("form-control", {
                                                    invalid: errors.from
                                                })}/>
                                            <span className="text-danger">{errors.from}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="to">To</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.to}
                                                id="turner-update-to"
                                                type="text"
                                                error={errors.to}
                                                className={classnames("form-control", {
                                                    invalid: errors.to
                                                })}/>
                                            <span className="text-danger">{errors.to}</span>
                                        </div>
                                    </div>
                                </form>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                <button
                                    form="update-turner"
                                    type="submit"
                                    className="btn btn-primary">
                                    Update Turner
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

TurnerUpdateModal.propTypes = {
    updateTurner: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    turner: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    auth: state.auth,
    turner: state.turner,
    errors: state.errors
});

export default connect(
    mapStateToProps,
    { updateTurner }
)(withRouter(TurnerUpdateModal));
