import React from 'react'
import classnames from "classnames";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { addTurner } from "../../actions/turnerActions";
import { withRouter } from "react-router-dom";
import { toast } from 'react-toastify';
import $ from 'jquery';

import 'react-toastify/dist/ReactToastify.css';

class TurnerAddModal extends React.Component {

    constructor() {
        super();
        this.state = {
            name: "",
            from: "",
            to: "",
            use: 0,
            errors: {},
        };
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            });
        }
        if (nextProps.turner !== undefined
            && nextProps.turner.turner !== undefined
            && nextProps.turner.turner.data !== undefined
            && nextProps.turner.turner.data.message !== undefined) {
            $('#add-turner-modal').modal('hide');
            toast(nextProps.turner.turner.data.message, {
                position: toast.POSITION.TOP_CENTER
            });
        }
    }

    onChange = e => {
        this.setState({ [e.target.id]: e.target.value });
    };

    onTurnerAdd = e => {
        e.preventDefault();
        const newTurner = {
            name: this.state.name,
            from: this.state.from,
            to: this.state.to,
            use: this.state.use,
        };
        this.props.addTurner(newTurner, this.props.history);
    };

    render() {
        const { errors } = this.state;
        return (
            <div>
                <div className="modal fade" id="add-turner-modal" data-reset="true">
                    <div className="modal-dialog modal-lg">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h4 className="modal-title">Add Turner</h4>
                                <button type="button" className="close" data-dismiss="modal">&times;</button>
                            </div>
                            <div className="modal-body">
                                <form noValidate onSubmit={this.onTurnerAdd} id="add-turner">
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="name">Name</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.name}
                                                id="name"
                                                type="text"
                                                error={errors.name}
                                                className={classnames("form-control", {
                                                    invalid: errors.name
                                                })}/>
                                            <span className="text-danger">{errors.name}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="use">Use</label>
                                        </div>
                                        <div className="col-md-9">
                                            <select id="use" onChange={this.onChange} value={this.state.use} error={errors.use}
                                                className={classnames("form-control", {
                                                    invalid: errors.use
                                                })}>
                                                <option value={1}>Enable</option>
                                                <option value={0}>Disable</option>
                                            </select>
                                            <span className="text-danger">{errors.use}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="from">From</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.from}
                                                error={errors.from}
                                                id="from"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.from
                                                })}
                                            />
                                            <span className="text-danger">{errors.from}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="to">To</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.to}
                                                error={errors.to}
                                                id="to"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.to
                                                })}
                                            />
                                            <span className="text-danger">{errors.to}</span>
                                        </div>
                                    </div>
                                </form>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                <button
                                    form="add-turner"
                                    type="submit"
                                    className="btn btn-primary">
                                    Add Turner
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

TurnerAddModal.propTypes = {
    addTurner: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    turner: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    auth: state.auth,
    turner: state.turner,
    errors: state.errors
});

export default connect(
    mapStateToProps,
    { addTurner }
)(withRouter(TurnerAddModal));
