import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { logoutUser } from "../../actions/authActions";
import Navbar from "../partials/Navbar";
import Sidebar from "../partials/Sidebar";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faList} from "@fortawesome/free-solid-svg-icons/faList";
import {Link} from "react-router-dom";
import {faUserAlt} from "@fortawesome/free-solid-svg-icons/faUserAlt";
import './adminlte.css';
import {faLink} from "@fortawesome/free-solid-svg-icons/faLink";
import {faChartArea} from "@fortawesome/free-solid-svg-icons/faChartArea";
import {faChartBar} from "@fortawesome/free-regular-svg-icons/faChartBar";
import {faUserCircle} from "@fortawesome/free-regular-svg-icons/faUserCircle";
import {faArrowAltCircleRight} from "@fortawesome/free-regular-svg-icons/faArrowAltCircleRight";
import axios from "axios";
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

import config from "../../config/config";

const serverUrl = config.serverUrl;

var feedChartData = [ 
  {
    title: 'Page A',
    visits: 4000,
  },
  {
    title: 'Page B',
    visits: 3000,
  },
  {
    title: 'Page C',
    visits: 2000,
  },
  {
    title: 'Page D',
    visits: 2780,
  },
  {
    title: 'Page E',
    visits: 1890,
  },
  {
    title: 'Page F',
    visits: 2390,
  },
  {
    title: 'Page G',
    visits: 3490,
  },
];

class Dashboard extends Component {

    constructor() {
        super();
        this.state = {
            turnerCount: 0,
            todayVisits: 0,
            weekVisits: 0,
            uniqueVisits: 0,
            totalVisits: 0,
            chartData_Visitor : feedChartData,
            todayInstalls: 0,
            weekInstalls: 0,
            uniqueInstalls: 0,
            totalInstalls: 0,
            chartData_Installer : feedChartData,
        };
    }

    componentDidMount() {

        this.getStatusInfos();
    }

    componentWillReceiveProps(nextProps) {

    }

    onLogoutClick = e => {
        e.preventDefault();
        this.props.logoutUser();
    };

    getStatusInfos() {
        axios
        .post(serverUrl + "/api/getinfo")
        .then(res => {
            this.setState({ 
                turnerCount: res.data.turner_count,
                todayVisits: res.data.visitorRes.today,
                weekVisits: res.data.visitorRes.week,
                uniqueVisits: res.data.visitorRes.unique,
                totalVisits: res.data.visitorRes.total,
                chartData_Visitor : res.data.visitorRes.chart_data,
                todayInstalls: res.data.installedRes.today,
                weekInstalls: res.data.installedRes.week,
                uniqueInstalls: res.data.installedRes.unique,
                totalInstalls: res.data.installedRes.total,
                chartData_Installer : res.data.installedRes.chart_data,
            });
        })
        .catch()

        // axios
        // .post("/api/getchartinfo")
        // .then(res => {
        //     console.log(res.data);
        //     feedChartData = res.data;
        // })
        // .catch()
    }

    render() {
        
        return (
            <div>
                <Navbar/>
                <div className="d-flex" id="wrapper">
                    <Sidebar/>
                    <div id="page-content-wrapper">
                        <div className="container-fluid">
                            <button className="btn btn-link mt-2" id="menu-toggle"><FontAwesomeIcon icon={faList}/></button>
                            <h1 className="mt-2 text-primary">Dashboard</h1>
                            <br/>
                            <div className="row px-2">
                                <div className="col-lg-3 col-6 p-sm-2">
                                    <div className="card bg-primary text-white shadow-lg">
                                        <div className="card-body">
                                            <h5 className="card-title">Users</h5>
                                            <p className="card-text">You can take a look info about Conexiones Seguras Administrators</p>
                                            <Link to="/users" className="btn btn-light"><FontAwesomeIcon className="text-primary" icon={faUserAlt}/> Go to Users</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2">
                                    <div className="card bg-secondary text-white shadow-lg">
                                        <div className="card-body">
                                            <h5 className="card-title">Turners</h5>
                                            <p className="card-text">Addm Remove or Modify redirect rules you wanna use.</p>
                                            <Link to="/turners" className="btn btn-light"> Go to Turners</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2">
                                    <div className="card bg-info text-white shadow-lg">
                                        <div className="card-body">
                                            <h5 className="card-title">Visitor Histories</h5>
                                            <p className="card-text">You can track histories for every visitors through this bridge.</p>
                                            <Link to="/visitorhistory" className="btn btn-light"> View History</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2">
                                    <div className="card bg-dark text-white shadow-lg">
                                        <div className="card-body">
                                            <h5 className="card-title">Event</h5>
                                            <p className="card-text">Lets celebrate our goals! Financial transactions and records here</p>
                                            <Link to="/event" className="btn btn-light"> Celebrate</Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row px-2">
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-info">
                                        <div className="inner">
                                            {/* <h3>{this.state.turnerCount}</h3>
                                            <p>Domain Turners</p> */}
                                            <h3>{this.state.totalVisits}</h3>
                                            <p>Total Visits</p>
                                        </div>
                                        <div className="icon">
                                        <FontAwesomeIcon icon={faLink} />
                                        </div>
                                        <a href="/visitorhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-success">
                                    <div className="inner">
                                        <h3>{this.state.todayVisits}</h3>
                                        <p>Today's visits</p>
                                    </div>
                                    <div className="icon">
                                        <FontAwesomeIcon icon={faChartBar} />
                                    </div>
                                    <a href="/visitorhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-warning">
                                    <div className="inner">
                                        <h3>{this.state.weekVisits}</h3>

                                        <p>Visits for week</p>
                                    </div>
                                    <div className="icon">
                                        <FontAwesomeIcon icon={faChartArea} />
                                    </div>
                                    <a href="/visitorhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-danger">
                                    <div className="inner">
                                        <h3>{this.state.uniqueVisits}</h3>

                                        <p>Total Unique Visitors</p>
                                    </div>
                                    <div className="icon"><FontAwesomeIcon icon={faUserCircle} />
                                    </div>
                                    <a href="/visitorhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div> 
                            </div>
                            <div className="row px-2">
                                <div style={{ width: '100%', height: 300 }}>
                                    <ResponsiveContainer>
                                    <AreaChart
                                        data={this.state.chartData_Visitor}
                                        margin={{
                                        top: 10,
                                        right: 30,
                                        left: 0,
                                        bottom: 0,
                                        }}
                                    >
                                        <CartesianGrid strokeDasharray="3 3" />
                                        <XAxis dataKey="title" />
                                        <YAxis />
                                        <Tooltip />
                                        <Area type="monotone" dataKey="visits" stroke="#8884d8" fill="#8884d8" />
                                    </AreaChart>
                                    </ResponsiveContainer>
                                </div>
                            </div>
                            <br/>
                            <div className="row px-2">
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-info">
                                        <div className="inner">
                                            {/* <h3>{this.state.turnerCount}</h3>
                                            <p>Domain Turners</p> */}
                                            <h3>{this.state.totalInstalls}</h3>
                                            <p>Total Insallers</p>
                                        </div>
                                        <div className="icon">
                                        <FontAwesomeIcon icon={faLink} />
                                        </div>
                                        <a href="/installedhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-success">
                                    <div className="inner">
                                        <h3>{this.state.todayInstalls}</h3>
                                        <p>Today's installers</p>
                                    </div>
                                    <div className="icon">
                                        <FontAwesomeIcon icon={faChartBar} />
                                    </div>
                                    <a href="/installedhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-warning">
                                    <div className="inner">
                                        <h3>{this.state.weekInstalls}</h3>

                                        <p>Installers for week</p>
                                    </div>
                                    <div className="icon">
                                        <FontAwesomeIcon icon={faChartArea} />
                                    </div>
                                    <a href="/installedhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6 p-sm-2 shadow-lg">
                                    <div className="small-box bg-danger">
                                    <div className="inner">
                                        <h3>{this.state.uniqueInstalls}</h3>

                                        <p>Total Unique Installers</p>
                                    </div>
                                    <div className="icon"><FontAwesomeIcon icon={faUserCircle} />
                                    </div>
                                    <a href="/installedhistory" className="small-box-footer">More info <FontAwesomeIcon icon={faArrowAltCircleRight} /></a>
                                    </div>
                                </div> 
                            </div>
                            <div className="row px-2">
                                <div style={{ width: '100%', height: 300 }}>
                                    <ResponsiveContainer>
                                    <AreaChart
                                        data={this.state.chartData_Installer}
                                        margin={{
                                        top: 10,
                                        right: 30,
                                        left: 0,
                                        bottom: 0,
                                        }}
                                    >
                                        <CartesianGrid strokeDasharray="3 3" />
                                        <XAxis dataKey="title" />
                                        <YAxis />
                                        <Tooltip />
                                        <Area type="monotone" dataKey="visits" stroke="#8884d8" fill="#8884d8" />
                                    </AreaChart>
                                    </ResponsiveContainer>
                                </div>
                            </div>
                            
                            
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

Dashboard.propTypes = {
    logoutUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    auth: state.auth
});

export default connect(
    mapStateToProps,
    { logoutUser }
)(Dashboard);
